@import "../../assets/scss/app.scss";

.chrono {
  height: 100%;

  .inner {
    background-color: rgba(33, 35, 31, 0.75);
    max-width: 1120px;
    width: 100%;
    height: 100%;
    padding: 20px;
    margin: 0 auto;
    box-sizing: border-box;

    .title {
      @include h1();
      text-align: center;
    }

    .items {
      margin-top: 20px;
    }
  }
}