@import "../../assets/scss/app.scss";

.header {
  height: 60px;
  background: #21231f;

  .inner {
    width: 100%;
    height: 100%;
    padding: 0 16px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include desktop() {
      padding: 0 24px;
    }


    @include desktop() {
      max-width: 1120px;
      width: 100%;
      margin: 0 auto;
    }

    .logo {
      display: block;

      img {
        display: block;
        height: 54px;
        width: auto;
      }
    }

    .rightBlock {
      display: flex;
      align-items: center;

      .links {
        display: flex;
        align-items: center;

        .link {
          text-decoration: none;
          color: #FFF;

          &:not(:first-child) {
            margin-left: 20px;
          }
        }

      }

    }

  }

}